import React, { Component } from 'react';
import FormGenerator from './FormGenerator';
import { MAKESITEENTRY } from '../../graphql/mutation/remote';
import InputAdornment from '@material-ui/core/InputAdornment';

class CreateSiteEntry extends Component {
    render() {
        const { id: siteId } = this.props.match.params;
        const repeater = {
            quantity: {
                type: "number",
                default: ""
            },
            cost: {
                type: "number",
                default: "",
                startAdornment: <InputAdornment position="start">₹</InputAdornment>
            },
            paid: {
                type: Boolean,
                default: false
            }
        };

        const defaults = {
            mistri: {
                default: repeater
            },
            labour: {
                default: repeater
            },
            eit: {
                default: repeater
            },
            morang: {
                default: repeater
            },
            githi: {
                default: repeater
            },
            cement: {
                default: repeater
            },
            saria: {
                default: repeater
            },
            dust: {
                default: repeater
            },
            other: {
                default: {
                    quantity: {
                        type: "text",
                        default: "",
                    },
                    cost: {
                        type: "number",
                        default: ""
                    },
                    paid: {
                        type: Boolean,
                        default: false
                    }
                }
            },
            createdAt: {
                type: "date",
                default: new Date()

            }
        };
        return (
            <FormGenerator {...this.props} defaults={defaults} submitText="Create Entry" mutation={MAKESITEENTRY} mutateFunc={(fn, data) => { return fn({variables: {siteId, data}});}} title="Create a new entry"></FormGenerator>
        )
    }
};

export default CreateSiteEntry;



